/* eslint-disable react/prefer-stateless-function */
import { TablePagination } from '@material-ui/core';
import { connect } from 'react-redux';
import { Component } from 'react';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { AppDispatch, RootState } from '../../state/store';
import { setRowsPerPage } from '../../state/slices/pagination.slice';

interface IPagination extends RouteComponentProps {
    count: number;
    pageNum: number;
    rowsPerPage?: number;
    handleSetRowsPerPage?: (arg0: number) => void;
}

class Pagination extends Component<IPagination> {
    constructor(props: IPagination) {
        super(props);
        this.handlePageChange = this.handlePageChange.bind(this);
        this.handleChangeRowsPerPage = this.handleChangeRowsPerPage.bind(this);
    }

    handleChangeRowsPerPage(event: React.ChangeEvent<HTMLInputElement>) {
        const { handleSetRowsPerPage } = this.props;
        handleSetRowsPerPage(parseInt(event.target.value, 10));
    }

    handlePageChange(
        event: React.MouseEvent<HTMLInputElement>,
        lastPage: number
    ) {
        const { history } = this.props;
        const nextPage = lastPage + 1;
        history.push(`/books/${nextPage}`);
    }

    render() {
        const { count, rowsPerPage, pageNum } = this.props;

        if (!count) {
            return null;
        }
        return (
            <TablePagination
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={count}
                rowsPerPage={rowsPerPage}
                page={pageNum === 0 ? 1 : pageNum - 1}
                onChangePage={this.handlePageChange}
                onChangeRowsPerPage={this.handleChangeRowsPerPage}
            />
        );
    }
}

const mapStateToProps = (state: RootState) => ({
    rowsPerPage: state.pagination.rowsPerPage
});

const mapDispatchToProps = (dispatch: AppDispatch) => ({
    handleSetRowsPerPage: (arg: number) => dispatch(setRowsPerPage(arg))
});

export const PaginationConnected = withRouter(
    connect(mapStateToProps, mapDispatchToProps)(Pagination)
);
